import {
    required,
    numeric
} from 'vuelidate/lib/validators';

export const validationRules = {
    country: {
        required,
        objectStructure(value) {
            console.log('validator', value)
            if (!value.value || !value.iso) { return false }
            console.log(true)
            return true
        }
    },
    phone_number: {
        required,
        numeric,
        async isUniquePhoneNumber() {
            if (!this.country.value || !this.phone_number) return true
            const response = await this.$store.dispatch('client/ACTION_CLIENT_CHECK_PHONE_EXISTS', {
                phone_code: this.country.value,
                phone: this.phone_number
            })
            return response.data.status
        }
    }
}
