<template>
    <fieldset class="form-input__fieldset">
        <legend class="form-input__legend">
            Phone Number
        </legend>
        <input
            v-model="val"
            class="form-input__input"
            :class="{ 'error-border': validationError, 'success': isValid }"
            inputmode="numeric"
            pattern="[0-9]*"
            name="phone_number"
            @input="onChange($event)"
        >
        <span
            v-if="value.length > 0"
            @click="clearInput"
        >
            <CloseIcon
                class="form-input__append-icon"
            />
        </span>
        <div class="form-input__error">
            {{ validationError }}
        </div>
    </fieldset>
</template>

<script>
import CloseIcon from 'mdi-vue/Close.vue';
import { mapPhoneFields } from '@/store/modules/phone'

export default {
    name: 'PhoneNumberInput',
    components: {
        CloseIcon
    },
    props: {
        value: {
            type: String
        },
        type: {
            type: String
        },
        name: {
            type: String
        },
        field: {
            type: String
        },
        errors: {
            type: String
        },
        isValid: {
            type: Boolean
        }
    },
    data() {
        return {
            val: ''
        }
    },
    computed: {
        ...mapPhoneFields([
            'form_data.country',
            'form_data.phone_number'
        ]),
        validationError() {
            return this.errors ? this.errors : '';
        }
    },
    methods: {
        onChange(event) {
            this.$emit('update:field', event.target.value);
        },
        emitEvent() {
            this.$emit('clear');
        },
        clearInput() {
            this.val = ''
            this.$emit('update:field', '');
        }
    }
};
</script>

