<template>
    <div class="phone_wrapper">
        <phone-number-valid
            v-if="verified"
            :phone-code="country.value"
            :phone="phone_number"
        />
        <div v-else>
            <phone-number-field
                v-if="!started"
                v-model="phone"
            />
            <phone-number-pin v-else />
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import { mapPhoneFields } from '@/store/modules/phone'
import PhoneNumberValid from './components/PhoneNumberValid'
import PhoneNumberField from './components/PhoneNumberField'
import PhoneNumberPin from './components/PhoneNumberPin'

// VUEX

export default {
    name: 'PhoneNumber',
    components: {
        PhoneNumberValid,
        PhoneNumberField,
        PhoneNumberPin
    },
    props: {
        country: {
            type: Object,
            phone_code: {
                type: Number
            },
            iso: {
                type: String
            }
        }
    },
    data: () => ({
        phone: {}
    }),
    computed: {
        ...mapState('phone', {
            started: 'started',
            verified: 'verified'
        }),
        ...mapPhoneFields([
            'form_data.country',
            'form_data.phone_number'
        ])
    }
}
